body, html {
    height: 100%;
    margin: 0;
    overflow: auto;
    background: rgb(242, 234, 224);
}

@import url('https://fonts.googleapis.com/css2?family=Rubik:ital,wght@0,300..900;1,300..900&display=swap');
.containerreg {
    height: 100%;
    min-height: 972px;


  
}
.fullscreen-image-containerreg {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden; /* Добавлено, чтобы скрыть части левой и правой частей, выходящие за границы */
    min-height: 972px;
}

.fullscreen-imagereg {
    width: 100%;
    height: 100%;
    object-fit: cover;
    filter: blur(10px);
    min-height: 972px;
}
@keyframes slideIn {
    0% {
      transform: translateX(-100%);
    }
    100% {
      transform: translateX(0);
    }
  }
  

.half-login-leftreg,
.half-login-rightreg {
    position: absolute;
    height: 100%;
    width: 50%;
    top: 0;
}

.half-login-leftreg {
    right: 0;
    z-index: 1;
    animation: slideIn 1s ease-out;
}

.half-login-leftreg::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(5, 41, 82, 0.73);
    filter: blur(3px);
    backdrop-filter: blur(18px);
    z-index: -1;
 
}

.half-login-rightreg {
    left: 0;
    z-index: 1;
}

.half-login-rightreg::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
 
}
